<template>
  <div>
    <v-row>
      <v-col cols="3">
        <v-btn class="mb-4" block @click="subscriptionDialog = true, subscriptionItem = {}">
          <v-icon>mdi-shape-square-plus</v-icon>&nbsp;New Subscription
        </v-btn>
        <v-btn
          :key="index" v-for="(s, index) of mqttConfig.subscriptions"
          :class="`subscription-item border-left ${selectedTopics.includes(s.topic) ? 'active': ''}`"
          :style="`border-color: ${s.color}`" >
          {{s.topic}}
          <span class="white--text d-flex">
            <v-switch right v-model="s.enable" @change="switchEnable(s)" class="mqtt-switch-btn"></v-switch>
            <v-icon small color="blue" @click="editSubscription(index)" @click.stop="stopTheEvent"> mdi-pencil-circle </v-icon>&nbsp;
            <v-icon small color="red" @click="removeSubscription(index)" @click.stop="stopTheEvent"> mdi-close-circle </v-icon>
          </span>
        </v-btn>
      </v-col>
      <v-col cols="9">
        <v-card elevation="2">
          <v-card-text>
            <v-row>
              <v-spacer></v-spacer>
              <div class="text-right mb-5">
                <v-btn text small color="warning" @click="messages = []">Clear</v-btn>

                <v-btn-toggle v-model="toggle" mandatory>
                  <v-btn small>All</v-btn>
                  <v-btn small>Received</v-btn>
                  <v-btn small>Published</v-btn>
                </v-btn-toggle>
              </div>
            </v-row>
            <div id="message-container">
              <div v-for="(m, index) in filteredMessages" :key="index" class="message">
                <v-chip
                  v-if="m.type === 'publish'"
                  :color="m.color"
                  :class="`${m.type}-message`"
                  style="border-radius: 5px;"
                >
                  Topic: {{m.topic}} <br /> Message: {{m.text}}
                </v-chip>
                <v-chip
                  v-if="m.type === 'receive'"
                  :class="`${m.type}-message border-left`"
                  :style="`border-color: ${m.color}; border-radius: 5px;`"
                >
                  Topic: {{m.topic}} <br /> Message: {{m.text}}
                </v-chip>
              </div>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-form style="width: 100%" v-model="valid" ref="form" lazy-validation>
              <v-text-field v-model="topic" label="Topic" :rules="[required]"></v-text-field>
              <v-text-field @keyup.enter.native="sendMessage" v-model="message" label="Message" :rules="[required]">
                <v-icon slot="append" color="primary" @click="sendMessage" > mdi-send </v-icon>
              </v-text-field>
            </v-form>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="subscriptionDialog" max-width="400px">
      <subscription-form :onCancel="onCloseSubscription" :onSave="onSaveSubscription" :item="subscriptionItem"></subscription-form>
    </v-dialog>
  </div>
</template>

<script>
import SubscriptionForm from "./SubscriptionForm";
import axios from "../axios";
import * as _ from "lodash";
import { mqttWildcard } from '../utils/utils';

export default {
  components: {
    SubscriptionForm,
  },
  data:()=>({
    subscriptionDialog: false,
    selectedTopics: [],
    required: (value) => !!value || "Required.",
    message: '',
    topic: '',
    toggle: 0,
    valid: false,
    messages: [],
    subscriptionItem: {},
    editedIndex: -1,
  }),
  mounted() {
    this.socket = new WebSocket('ws://localhost:8080');
    this.socket.addEventListener('message', ({data}) => {
      const message = JSON.parse(data);
      this.messages.push({...message, type: 'receive'});
    });
  },
  computed: {
    mqttConfig() {
        return this.$store.state.mqttConfig;
    },
    filteredMessages() {
      let messages = [...this.messages];
      if (this.toggle === 1) {
        messages = this.messages.filter(m => m.type === 'receive');
      }
      if (this.toggle === 2) {
        messages = this.messages.filter(m => m.type === 'publish');
      }
      if (this.selectedTopics.length > 0) {
        messages = messages.filter(m => {
            let result = false;
            for (const selected of this.selectedTopics) {
                const isMatch = mqttWildcard(m.topic, selected);
                if (isMatch) {
                    result = true;
                    break;
                }
            }
            return result;
        });
      }
      messages.forEach(m=>{
        if(m.type === "receive"){
          const index = this.mqttConfig.subscriptions.findIndex(s=> s.topic == m.topic);
          m.color = index > -1 ? this.mqttConfig.subscriptions[index].color : "primary";
        }else{
          m.color = "primary"
        }
      });
      return messages;
    }
  },
  methods: {
    switchEnable({topic, enable}){
      if(enable) {
          this.selectedTopics.push(topic);
      } else {
          const index = this.selectedTopics.indexOf(topic);
          this.selectedTopics.splice(index, 1);
      }
    },
    removeSubscription(index) {
      delete this.mqttConfig.subscriptions.splice(index, 1);
      this.onSave();
    },
    editSubscription(index){
      this.subscriptionItem = _.cloneDeep(this.mqttConfig.subscriptions[index]);
      this.subscriptionDialog = true;
      this.editedIndex = index;
    },
    sendMessage() {
      this.$refs.form.validate();
      const isValid = this.$refs.form.validate();
      if (!isValid) {
        return;
      }
      const data = {topic: this.topic, message: this.message};
      this.messages.push({type: 'publish', text: this.message, topic: this.topic});
      this.message = '';
      axios.post('mqtt/send-message', data).then(r => {
        const container = this.$el.querySelector("#message-container");
        container.scrollTop = container.scrollHeight;
      });
    },
    onCloseSubscription() {
      this.subscriptionDialog = false;
      this.subscriptionItem = {};
      this.editedIndex = -1;
    },
    onSaveSubscription(data) {
      if (this.editedIndex > -1) {
        this.mqttConfig.subscriptions[this.editedIndex] = data;
        this.onSave();
        this.onCloseSubscription();
      } else {
        this.onCreateSubscription(data);
      }
    },
    onSave() {
        axios.put('mqtt-config', this.mqttConfig).then(r => {
            this.$toasted.show('Success');
            this.$store.commit('changeMqttConfig', this.mqttConfig);
            this.selectedTopics = this.mqttConfig.subscriptions.map(s => {
                if (s.enable) {
                    return s.topic;
                }
            })
        });
    },
    onCreateSubscription(data) {
      axios.post('mqtt-config/subscription', data).then(r => {
          this.mqttConfig.subscriptions.push(data)
          this.$store.commit('changeMqttConfig', this.mqttConfig);
          this.subscriptionDialog = false;
      });
    },
    stopTheEvent(event){
      event.stopPropagation();
    }
  }
};
</script>

<style>
#message-container {
  height: 70vh;
  overflow: auto;
}
.message {
  width: 100%;
  display: block;
  overflow: auto;
  margin-bottom: 10px;
}
.message .v-size--default {
  height: 48px;
}
.publish-message {
  display: block;
  float: right;
}
.receive-message {
  display: block;
  float: left;
}
.subscription-item {
  width: 100%;
  text-align: center;
  margin-bottom: 10px;
  text-transform: unset;

}
.subscription-item .v-btn__content {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.message .v-chip__content {
  text-align: left;
}
.subscription-item .v-btn__content {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.border-left {
  border-left: 1px solid;
  border-width: 4px!important;
}
.active {
  background-color: #666666 !important;
}
.mqtt-switch-btn {
  margin-top: 22px;
}
.mqtt-switch-btn .v-input--switch__track {
  border-radius: 8px;
  width: 30px;
  height: 10px;
  left: 2px;
  position: absolute;
  opacity: .6;
  right: 2px;
  top: calc(50% - 7px);
}
.mqtt-switch-btn .v-input--switch__thumb {
  border-radius: 50%;
  top: calc(50% - 10px);
  height: 15px;
  position: relative;
  width: 15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
</style>



